.article-card {
  display: none;
  border-top: 1px solid #E9E9E9;
  border-bottom: 1px solid #E9E9E9;
  padding: 20px 0;
}
@media (max-width: 1024px) {
  .article-card {
    display: block;
    margin-bottom: 20px;
    padding: 16px 0;
  }
}
@media (max-width: 767px) {
  .article-card {
    padding: 7px 0;
  }
}
.article-card:hover {
  transform: scale(1.01);
  transition: transform 0.5s;
}
.article-card__wrap {
  position: relative;
}
.article-card__link {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  color: #000000;
  text-decoration: none;
  outline: none;
  cursor: pointer;
}
@media (max-width: 767px) {
  .article-card__link {
    align-items: center;
  }
}
.article-card__img {
  width: 136px;
  min-height: 100px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
@media (max-width: 767px) {
  .article-card__img {
    width: 76px;
    min-height: 64px;
  }
}
.article-card__body {
  width: 67%;
  padding-left: 16px;
}
.article-card__title {
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  margin: 23px 0 0 0;
  padding: 0;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  -webkit-line-clamp: 3;
  text-overflow: ellipsis;
}
@media (max-width: 767px) {
  .article-card__title {
    margin-top: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    -webkit-line-clamp: 4;
  }
}
.article-card .stamp {
  position: absolute;
  bottom: 0;
  left: 152px;
  width: auto;
}
@media (max-width: 767px) {
  .article-card .stamp {
    display: none;
  }
}
.article-card .tag {
  position: absolute;
  top: 0;
  left: 152px;
}
@media (max-width: 767px) {
  .article-card .tag {
    display: none;
  }
}
.article-card .bookmark {
  position: absolute;
  top: 0;
  right: 0;
}
@media (max-width: 767px) {
  .article-card .bookmark {
    display: none;
  }
}